import { useStaticQuery, graphql } from 'gatsby';
import { Post } from 'models/post';
import { SupportedLanguages } from 'models/site';

export const useAllPosts = (lang: SupportedLanguages = 'ko') => {
  const data = useStaticQuery(graphql`
    query AllPostsQuery {
      allMarkdownRemark(sort: { fields: frontmatter___date, order: DESC }) {
        edges {
          node {
            excerpt(pruneLength: 300)
            fields {
              path
              lang
              slug
            }
            frontmatter {
              title
              thumbnail {
                childImageSharp {
                  fixed(width: 320, height: 320, quality: 100, cropFocus: CENTER) {
                    src
                  }
                  fluid {
                    src
                  }
                }
              }
              tags
              date(formatString: "YYYY-MM-DD")
              categories
            }
          }
        }
      }
    }
  `);

  const frontmatters: Post[] = data.allMarkdownRemark.edges
    .map(({ node }: { node: Post }) => node)
    .filter((p: Post) => p.fields.lang === lang);

  return frontmatters;
};
